<template>
  <section id="container">
    <div id="dashboard">
      <div id="left-side">
        <img src="../assets/logo-community-nospace.png" alt="Logo Comunidad Global One More" id="logo" />
        <h1>Bienvenido, {{ userName }}.</h1>
        <div id="cont-buttons">
          <RouterLink to="/welcomeadmin" class="button">
            <button>
              <font-awesome-icon icon="layer-group" class="icon_dashboard" />
              Estado
            </button>
          </RouterLink>
          
          <button class="logoutButton" @click="cerrarSesion">
            <font-awesome-icon icon="arrow-right-from-bracket" class="icon_dashboard" />
            Cerrar Sesin
          </button>
        </div>
      </div>
      <div id="right-side">
        <div id="cont-list">
          <!-- Renderizamos la lista de usuarios aqu -->
          <!-- Tabla para mostrar usuarios -->
          <table>
            <thead>
              <tr>
                <th>#</th>
                <th>Nombre</th>
                <th>Correo</th>
                <th>Telfono</th>
                <th>Estado</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(user, index) in users" :key="user.id">
                <td>{{ index + 1 }}</td>
                <td>{{ user.name }}</td>
                <td>{{ user.email }}</td>
                <td>{{ user.mobile }}</td>
                
                <td>
                  <strong :style="{ color: user.isActive ? 'green' : 'red' }"> {{ user.isActive ? 'Activo' : 'Inactivo' }}</strong>

                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import mitt from "mitt";
import {
  collection,
  getDocs,
  updateDoc,
  doc,
  onSnapshot,
} from "firebase/firestore";
import { getAuth, signOut } from "firebase/auth";
import { db } from "@/main";

export default {
  name: "InfoAdmin",
  data() {
    return {
      users: [], // Almacenar la lista de usuarios
      userId: localStorage.getItem("userId"), // Recupera el userId desde localStorage
      userName: localStorage.getItem("userName"), // Recupera el nombre del usuario desde localStorage
      emitter: mitt(), // Crear una instancia de mitt
    };
  },
  methods: {
    async fetchUsers() {
      try {
        const querySnapshot = await getDocs(collection(db, "users"));
        const usersList = [];
        querySnapshot.forEach((doc) => {
          const userData = { id: doc.id, ...doc.data() };
          if (userData.role !== "admin") {
            usersList.push(userData);
          }
        });
        this.users = usersList;
      } catch (error) {
        console.error("Error al obtener la lista de usuarios:", error);
      }
    },

    async toggleActive(user) {
      try {
        const updatedStatus = !user.isActive;
        user.isActive = updatedStatus;
        const userRef = doc(db, "users", user.id);
        await updateDoc(userRef, {
          isActive: updatedStatus,
        });
        alert(
          `Usuario ${updatedStatus ? "activado" : "desactivado"} correctamente.`
        );
      } catch (error) {
        console.error("Error al cambiar el estado de usuario:", error);
        alert("Hubo un error al cambiar el estado del usuario.");
      }
    },

    async cerrarSesion() {
      const auth = getAuth();

      try {
        await signOut(auth);
        console.log("Usuario cerrado sesin con xito");
        localStorage.removeItem("userId");
        localStorage.removeItem("userName");
        this.$router.replace("/login");
      } catch (error) {
        console.error("Error al cerrar sesin:", error);
      }
    },
  },
  mounted() {
    this.fetchUsers();

    // Usamos mitt para escuchar los cambios en la base de datos
    this.unsubscribe = onSnapshot(collection(db, "users"), (querySnapshot) => {
      const usersList = [];
      querySnapshot.forEach((doc) => {
        const userData = { id: doc.id, ...doc.data() };
        if (userData.role !== "admin") {
          usersList.push(userData);
        }
      });
      this.users = usersList; // Actualizamos la lista de usuarios
      this.emitter.emit("usersUpdated", usersList); // Emitimos un evento con mitt
    });
  },

  // Usamos onBeforeUnmount para limpiar el listener cuando el componente se destruya
  BeforeUnmount() {
    if (this.unsubscribe) {
      this.unsubscribe(); // Limpiar el listener de Firestore
    }
  },
};
</script>

<style lang="sass" scoped>
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap')

#container
    display: flex
    justify-content: center
    align-items: center
    height: 100vh
    background: linear-gradient(to bottom, #0b004b, #b83aff)
    font-family: 'Roboto', sans-serif
    #dashboard
        display: flex
        width: 90%
        height: 90%
        background: white
        border-radius: 10px
        box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2)
        #left-side
            display: flex
            justify-content: start
            align-items: center
            flex-direction: column
            width: 30%
            height: 100%
            background: #0704A5
            border-radius: 10px 0 0 10px
            #logo
                width: 30%
                padding-top: 20px
            h1
                font-size: 1.5rem
                color: white
                text-align: center
                margin-bottom: 100px
            #cont-buttons
                display: flex
                flex-direction: column
                align-items: center
                width: 100%
                gap: 25px
                .button
                    width: 100%
                    position: relative
                    left: 5%
                button
                    width: 90%
                    height: 50px
                    background: #e1e1ef
                    border-radius: 5px
                    color: #0704A5
                    font-size: 1rem
                    font-weight: 600
                    outline: none
                    cursor: pointer
                    border: none
                    transition: all 0.3s ease-in-out
                    display: flex
                    align-items: center
                    justify-content:  space-evenly
                    &:hover
                        background: #6a42ff
                        color: white
                        transform: scale(1.05)
                        font-size: 1.1rem
                    .icon_dashboard
                        font-size: 1.7rem
                        position: relative
                        right: 16%
                .logoutButton
                  background: #fb573b
                  color: white
        #right-side
            display: flex
            justify-content: center
            align-items: center
            width: 70%
            height: 100%
            background: white
            border-radius: 0 10px 10px 0
            #cont-list
                width: 90%
                height: 80%
                background: #e1e1ef
                border-radius: 10px
                margin: 40px
                padding: 20px
                overflow: auto
                table
                    width: 100%
                    border-collapse: collapse
                    text-align: left
                    thead
                        background-color: #f4f4f4
                    th, td
                        padding: 10px
                        border: 1px solid #ddd
                    tbody
                        tr:nth-child(even)
                            background-color: #f9f9f9
                        tr:hover
                            background-color: #f1f1f1
                        button
                            padding: 5px 10px
                            background-color: #4CAF50
                            color: white
                            border: none
                            cursor: pointer
                            font-size: 14px
                            border-radius: 5px
                            cursor: pointer
                            &:hover
                                opacity: 0.6

                    /* Estilos para el botn Activar */
                    .btn-deactivate
                        background-color: #4CAF50
                        color: white

                    /* Estilos para el botn Desactivar */
                    .btn-activate
                        background-color: #f44336
                        color: white
</style>
