<template>
  <section id="container">
    <div id="dashboard">
      <div id="left-side">
        <img id="logo" src="../assets/logo-community-nospace.png" alt="Logo" />
        <h1>Hola, {{ userName }}.</h1>
        <div id="cont-buttons">

          <RouterLink to="/welcome" class="button">
            <button>
              <font-awesome-icon icon="layer-group" class="icon_dashboard" />
              Dashboard
            </button>
          </RouterLink>

          <router-link to="/UserInfor" class="button">
            <button>
              <font-awesome-icon icon="book" class="icon_dashboard" />
              Informacin
            </button>
          </router-link>

          <router-link to="/UserPay" class="button">
            <button>
              <font-awesome-icon icon="bitcoin-sign" class="icon_dashboard" />
              Comprobante
            </button>
          </router-link>

          <router-link to="/UserConfig" class="button">
            <button>
              <font-awesome-icon icon="gears" class="icon_dashboard" />
              Configuracin
            </button>
          </router-link>

          <button class="logoutButton" @click="cerrarSesion">
            <font-awesome-icon icon="arrow-right-from-bracket" class="icon_dashboard" />
            Cerrar Sesin
          </button>
        </div>

        <!-- Men desplegable RESPONSIVE -->
        <div class="dropdown">
          <div class="dropdown-btn" @click="toggleDropdown">
            <font-awesome-icon icon="bars" class="icon_dashboard" />
          </div>
          <ul class="dropdown-menu" v-if="isDropdownOpen">
            <li>
              <h1 class="dropdown-title">Hola, {{ userName }}.</h1>
            </li>

            <li>
              <RouterLink to="/Welcome">
                <font-awesome-icon icon="layer-group" class="icon_dashboard" />
                Dashboard
              </RouterLink>
            </li>

            <li>
              <router-link to="/UserInfor">
                <font-awesome-icon icon="book" class="icon_dashboard" />
                Informacin
              </router-link>
            </li>

            <li>
              <router-link to="/UserPay">
                <font-awesome-icon icon="bitcoin-sign" class="icon_dashboard" />
                Comprobante
              </router-link>
            </li>

            <li>
              <router-link to="/UserConfig">
                <font-awesome-icon icon="gears" class="icon_dashboard" />
                Configuracin
              </router-link>
            </li>

            <li @click="cerrarSesion">
              <font-awesome-icon icon="arrow-right-from-bracket" class="icon_dashboard" />
              Cerrar Sesin
            </li>
            <!-- Botn para cerrar el men -->
            <button class="close-btn" @click="toggleDropdown">
              x
            </button>
          </ul>
        </div>
      </div>
      <div id="right-side">
        <div id="cont-list">
          <table>
            <thead>
              <tr>
                <th>#</th>
                <th>Nombre</th>
                <th>Estado</th>
              </tr>
            </thead>
            <tbody>
              <!-- Iterar sobre los usuarios filtrados -->
              <tr v-for="(user, index) in filteredUsers" :key="user.id">
                <td>{{ index + 1 }}.</td>
                <td>{{ user.name }}</td>
                <td>
                  <strong :style="{ color: user.isActive ? 'green' : 'red', textDecoration: 'underline' }"> {{
                    user.isActive ? 'Activo' : 'Inactivo' }}</strong>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
    <a href="https://wa.me/1234567890" class="whatsapp-button" target="_blank">
      <img src="https://upload.wikimedia.org/wikipedia/commons/thumb/6/6b/WhatsApp.svg/512px-WhatsApp.svg.png"
        alt="WhatsApp Logo" />
    </a>
  </section>
</template>

<script>
import { collection, query, where, onSnapshot } from "firebase/firestore";
import { getAuth, signOut } from "firebase/auth";
import { db } from "@/main";

export default {
  name: "InactiveUsers",
  data() {
    return {
      userId: localStorage.getItem("userId"),
      userName: localStorage.getItem("userName"),
      inactiveUsers: [], 
      isDropdownOpen: false,
    };
  },
  computed: {
    filteredUsers() {
      return this.inactiveUsers;
    },
  },
  methods: {
    async cerrarSesion() {
      try {
        await signOut(getAuth());
        localStorage.clear();
        this.$router.replace("/login");
      } catch (error) {
        console.error("Error al cerrar sesin:", error);
      }
    },
    fetchInactiveUsers() {
      const usersRef = collection(db, "users");
      const q = query(usersRef, where("isActive", "==", false));

      onSnapshot(q, (querySnapshot) => {
        this.inactiveUsers = querySnapshot.docs
          .map(doc => ({ id: doc.id, ...doc.data() })) // Convierte los documentos en objetos
          .filter(user => user.role !== "admin"); // Ignora al rol "admin"
      });
    },
    toggleDropdown() {
      this.isDropdownOpen = !this.isDropdownOpen;
    },
  },
  mounted() {
    this.fetchInactiveUsers(); 
  },
};
</script>


