          display: flex
          flex-direction: column
          justify-content: center
          align-items: center
          width: 85%
          height: 95%
          
        .dropdown
          display: none
        @media screen and ( max-width: 500px)
          .dropdown
            display: block
            position: fixed
            top:5%
            right: 11%
            color: #fff
