<template>
  <div id="register">
    <img src="../assets/logo-community.png" alt="logo" class="logo" />
    <form @submit.prevent="submitRegister" class="container">
      <h1 class="title">Regstrate</h1>
      <div class="form-group">
        <font-awesome-icon icon="user" class="icon_register" />
        <input
          type="text"
          class="nameUser"
          v-model="nameUser"
          placeholder="Ingresa tu nombre y apellidos"
          required
        />
      </div>

      <div class="form-group">
        <font-awesome-icon icon="envelope" class="icon_register" />
        <input
          type="text"
          class="emailUser"
          v-model="emailUser"
          placeholder="Ingresa tu correo electrnico"
          required
        />
      </div>

      <div class="form-group">
        <font-awesome-icon icon="phone" class="icon_register" />
        <input
          type="number"
          class="movilUser"
          v-model="movilUser"
          placeholder="Ingresa tu nmero de celular"
          required
        />
      </div>

      <div class="form-group">
        <font-awesome-icon icon="key" class="icon_register" />
        <input
          type="password"
          class="passwordUser"
          v-model="passwordUser"
          placeholder="Ingresa tu contrasea"
          required
        />
      </div>

      <div class="form-group">
        <font-awesome-icon icon="key" class="icon_register" />
        <input
          type="password"
          class="passwordUser"
          v-model="confirmpasswordUser"
          placeholder="Confirma tu contrasea"
          required
        />
      </div>

      <div class="loginRegister">
        <p>
          Ya tienes cuenta?
          <router-link to="/login">Iniciar Sesin</router-link>
        </p>
      </div>
      <button class="registerButton" type="submit">Registrate</button>
    </form>
  </div>
</template>

<script>
// Importa las funciones necesarias de Firebase Authentication para manejar la autenticacin de usuarios.
import { getAuth, createUserWithEmailAndPassword } from "firebase/auth";

// Importa las funciones necesarias de Firebase Firestore para interactuar con la base de datos.
import { doc, setDoc } from "firebase/firestore";

import { useToast } from "vue-toastification";

// Importa la instancia de la base de datos `db` desde el archivo principal (asegrate de que la ruta sea correcta).
import { db } from "@/main";

// Exporta un componente Vue llamado "UserRegister".
export default {
  name: "UserRegister", // Nombre del componente.

  // Define los datos reactivos del componente.
  data() {
    return {
      nameUser: "", // Nombre del usuario.
      emailUser: "", // Correo electrnico del usuario.
      movilUser: "", // Nmero de mvil del usuario.
      passwordUser: "", // Contrasea del usuario.
      confirmpasswordUser: "", // Confirmacin de la contrasea.
    };
  },

  // Mtodos del componente.
  methods: {
    // Mtodo para validar si un correo electrnico es vlido.
    validateEmail(email) {
      // Expresin regular para validar correos electrnicos.
      const regex = /^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,}$/;
      return regex.test(email); // Devuelve true si el correo es vlido.
    },

    // Mtodo para validar si un nmero de mvil es vlido.
    validateMobile(mobile) {
      // Expresin regular para validar un nmero de mvil de 9 dgitos.
      const regex = /^[0-9]{9}$/;
      return regex.test(mobile); // Devuelve true si el mvil es vlido.
    },

    // Mtodo para manejar el registro del usuario.
    async submitRegister() {
      const toast = useToast();
      // Validaciones
      if (!this.validateEmail(this.emailUser)) {
        toast.error("Por favor, ingresa un correo vlido."); // Alerta si el correo no es vlido.
        return;
      }

      if (!this.validateMobile(this.movilUser)) {
        toast.error(
          "Por favor, ingresa un nmero de mvil vlido (9 dgitos)."
        ); // Alerta si el mvil no es vlido.
        return;
      }

      if (this.passwordUser !== this.confirmpasswordUser) {
        toast.error("Las contraseas no coinciden."); // Alerta si las contraseas no coinciden.
        return;
      }

      try {
        // Obtiene la instancia de autenticacin de Firebase.
        const auth = getAuth();

        // Crea un nuevo usuario con el correo y la contrasea proporcionados.
        const userCredential = await createUserWithEmailAndPassword(
          auth,
          this.emailUser,
          this.passwordUser
        );

        // Obtiene el ID nico del usuario registrado.
        const userId = userCredential.user.uid;

        // Guarda los datos del usuario en la coleccin "users" de Firestore.
        await setDoc(doc(db, "users", userId), {
          name: this.nameUser, // Nombre del usuario.
          email: this.emailUser, // Correo del usuario.
          mobile: this.movilUser, // Mvil del usuario.
          role: "user", // Rol del usuario (user o admin)
          isActive: false,
          link1: ["", "true"], // ["enlace1", "true"]
          link2: ["", "true"], // ["enlace2", "true"]
          linkTaken: ["", ""], // ["enlaceTomado", "nombre de ususario a quien pertenece"]
          createdAt: new Date(), // Fecha de registro del usuario.
        });

        // Muestra un mensaje de xito al usuario.
        // alert("Usuario registrado correctamente");
        toast.success("Usuario registrado correctamente");

        // Redirige al usuario a la pgina de inicio de sesin.
        this.$router.push("/login");
      } catch (error) {
        // Manejo de errores en caso de que falle el registro.
        console.error("Error al registrar usuario:", error);
        toast.error(
          "Hubo un error al registrar al usuario. Intenta nuevamente."
        );
      }
    },
  },
};
</script>

<style lang="sass" scoped>
#register
  height: 100%
  display: flex
  align-items: center
  flex-direction: column
  font-family: 'Roboto', sans-serif
  padding-bottom: 150px
  gap: 5px

.logo
  width: 17%
  height: auto

.title
  font-size: 2.5rem
  color: white
  text-align: center
  margin-bottom: 15%

.container
  display: flex
  flex-direction: column
  align-items: center
  width: 100%
  max-width: 330px
  background-color: rgba(255, 255, 255, 0.1) // Fondo translcido
  padding: 30px
  border-radius: 12px
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2)

  .form-group
    position: relative
    width: 100%
    margin-bottom: 20px

    .icon_register
      position: absolute
      top: 50%
      left: 10px
      transform: translateY(-50%)
      color: #0704A5

    input
      width: 85%
      padding: 10px
      padding-left: 40px
      border: 1px solid #ccc
      border-radius: 5px
      background-color: #D9D9D9
      color: #0704a5
      box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1)

      &::placeholder
        color: #6a42ff

      &:focus
        outline: none
        border-color: #b83aff

  .registerButton
    width: 40%
    padding: 10px
    border: none
    margin-top: 10%
    border-radius: 5px
    background-color: #0704A5
    color: white
    font-weight: bold
    cursor: pointer
    transition: background-color 0.3s ease
    &:hover
      background-color: #b83aff

.loginRegister
  display: flex
  flex-direction: column
  font-size: .8rem
  margin-top: 20px
  margin-right: 55px
  p
    color: white
    margin-bottom: 5px

    a
      color: #0704A5
      text-decoration: none
      font-weight: bold

/* MEDIA QUERIES */
@media (max-width: 768px) // Para tabletas
  .logo
    width: 25% // Ajusta el tamao del logo para pantallas medianas
    margin-bottom: 15px

  .title
    font-size: 2rem // Reduce ligeramente el tamao del ttulo

  .container
    padding: 20px // Reduce el padding
    max-width: 300px // Ajusta el ancho mximo para pantallas medianas

@media (max-width: 480px) // Para mviles
  .logo
    width: 35% // Haz el logo ms pequeo en pantallas pequeas
    margin-bottom: 10px

  .title
    font-size: 1.8rem // Ajusta an ms el tamao del ttulo

  .container
    padding: 15px // Reduce el padding para mviles
    max-width: 280px // Ajusta el ancho mximo para mviles

  .loginButton
    width: 60% // Botn ms ancho para facilitar interaccin en mviles
</style>
